import { Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { Carousel } from "../utils/carousels";
import { useSelector } from "react-redux";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "85px 6vw 65px 6vw",
    },
    titleTexts: {
        fontWeight: "bold !important",
    },
    titleSubText: {
        paddingBottom: "25px"
    },

    cardRoot: {
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "400px",
        padding: "25px",
        paddingTop: "50px",
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
    },
    cardImage: {
        width: "200px",
        height: "200px",
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        marginBottom: "25px",
        objectFit: "contain"
    },
    cardTitle: {
        textAlign: "center",
        lineHeight: "30px !important"
    },
    cardDescription: {
        textAlign: "center",
        lineHeight: "35px",
        color: "grey !important"
    },
}));

function CourseCard(props) {
    const classes = useStyles();
    const { item, width, height, marginRight } = props;
    return (
        <Grid item className={classes.cardRoot} sx={{ width: `${width}px`, marginRight: `${marginRight}px` }} >
            <img src={item.imageSource} className={classes.cardImage} />
            <Typography variant="subtitle1" className={classes.cardTitle} >
                {item.name}
            </Typography>
            <Typography variant="body1" className={classes.cardDescription} >
                {item.platform}
            </Typography>
        </Grid >)
}

function Courses() {
    const classes = useStyles();
    const data = useSelector(state => state.courses.data);
    return (
        <Grid container className={classes.root} id="courses">
            <Grid item xs={12}>
                <Typography variant="h2" className={classes.titleTexts}>
                    Courses
                </Typography>
            </Grid>
            <Grid item>
                <Typography variant="h6" className={classes.titleSubText}>
                    I love self-studying. Here are some courses I have done
                </Typography>
            </Grid>
            <Carousel
                CarouselCardElement={CourseCard}
                data={data}
                perIndexLength={3}
                widthMultiplier={0.32}
                marginRightMultiplier={0.02} />
        </ Grid >
    );
}

export default Courses;
