import { Grid, List, ListItem, ListItemText, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { Carousel } from "../utils/carousels";
import { useSelector } from "react-redux";
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';

const useStyles = makeStyles((_) => ({
    root: {
        padding: "85px 6vw 65px 6vw",
    },
    titleTexts: {
        fontWeight: "bold !important",
    },
    titleSubText: {
        paddingBottom: "25px"
    },
    card: {
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "600px",
        padding: "25px",
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
        textAlign: "center",
    },
    cardImageCarouselRoot: {
        position: "relative",
        marginBottom: "55px"
    },
    cardImageCarousel: {
        width: "200px",
        height: "200px",
        marginRight: "auto",
        marginLeft: "auto",
        marginTop: "25px"
    },
    cardImageCard: {
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
        "& img": {
            width: "100%",
            height: "100%",
            objectFit: "contain"
        }
    },
    cardImageNavigation: {
        height: "0",
        padding: "0",
        "& h6": {
            verticalAlign: "middle",
            marginLeft: "15px",
            marginRight: "15px",
            fontSize: "20px",
            position: "absolute",
            bottom: "-45px",
        },
        "& div": {
            width: "40px",
            height: "40px"
        },
        "& .nextArrow": {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(110px,-50%)",

        },
        "& .previousArrow": {
            position: "absolute",
            top: "50%",
            right: "50%",
            transform: "translate(-110px,-50%)",
        }

    },
    cardTitle: {
        lineHeight: "30px !important",
        fontWeight: "bold !important"
    },
    cardList: {
        padding: "10px 0px 12.5px 0px !important",
        "& li": {
            padding: "0",
        },
        "& p": {
            display: "list-item",
            marginLeft: "auto",
            marginRight: "auto",
            listStyleType: "circle",
            listStylePosition: "inside"
        }
    }
}));


function ProjectCardImage(props) {
    const classes = useStyles();
    const { item, width, height, marginRight } = props;
    return (
        <Grid item className={classes.cardImageCard} sx={{ width: `${width}px`, height: height, marginRight: `${marginRight}px` }} >
            <img src={item} />
        </Grid >)
}

function ProjectCard(props) {
    const classes = useStyles();
    const { item, width, marginRight } = props;
    return (
        <Grid item className={classes.card} sx={{ width: `${width}px`, marginRight: `${marginRight}px` }} >
            <Carousel
                rootClassName={classes.cardImageCarouselRoot}
                carouselClassName={classes.cardImageCarousel}
                navigationClassName={classes.cardImageNavigation}
                CarouselCardElement={ProjectCardImage}
                data={item.imageSources}
                perIndexLength={1}
                NextButtonSvg={ArrowRightIcon}
                PreviousButtonSvg={ArrowLeftIcon}
                widthMultiplier={1}
                marginRightMultiplier={0.03}
            />
            <Typography variant="subtitle1" className={classes.cardTitle} >
                {item.name}
            </Typography>
            <List className={classes.cardList}>
                {
                    Object.entries(item.specification).map(([key, value], index) => {
                        return (
                            <ListItem key={index}>
                                <Typography variant="body1" >
                                    {key + " - " + value}
                                </Typography>
                            </ListItem>
                        )
                    })
                }
            </List>
            <Typography variant="body1" marginBottom={"5px"}>
                {item.scopesOfWork}
            </Typography>
            <Typography variant="body1" color={"grey"} >
                {item.category}
            </Typography>
        </Grid >)
}

function Projects() {
    const classes = useStyles();
    const data = useSelector(state => state.projects.data.reverse());
    return (
        <Grid container className={classes.root} id="projects">
            <Grid item xs={12}>
                <Typography variant="h2" className={classes.titleTexts}>
                    Projects
                </Typography>
            </Grid>
            <Grid item>
                <Typography variant="h6" className={classes.titleSubText}>
                    Several projects I made when being a student or employee
                </Typography>
            </Grid>
            <Carousel
                CarouselCardElement={ProjectCard}
                data={data}
                perIndexLength={3}
                widthMultiplier={0.32}
                marginRightMultiplier={0.02} />

        </ Grid >
    );
}

export default Projects;
