import { Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { useSelector } from "react-redux";
import StarIcon from '@mui/icons-material/Star';
import StarHalfIcon from '@mui/icons-material/StarHalf';
import StarBorderIcon from '@mui/icons-material/StarBorder';
import { useLayoutEffect } from "react";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "85px 6vw 65px 6vw",
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px"
    },
    cardDivider: {
        borderBottomWidth: "medium !important",
        maxWidth: "1000px"
    },
    card: {
        flexDirection: "column !important",
        alignItems: "center",
        justifyContent: "center",
        display: "flex",
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "85px",
        "& h6": {
            lineHeight: "1.3"
        }
    },
    starContainer: {
        justifyContent: "center",
        "& *": {
            marginLeft: "5px",
            marginRIght: "5px",
        }
    }
}));
function ProficiencyCard(props) {
    const classes = useStyles();
    const { item } = props;
    return (
        <Grid item xs={4} >
            <div className={classes.card}>
                <Typography variant="h6" >
                    {item.name}
                </Typography>
                <Grid container className={classes.starContainer}>
                    {
                        Array.from(Array(5), (e, i) => {
                            const substractedValue = item.value - i
                            if (substractedValue >= 1) return <StarIcon key={i} />
                            else if (substractedValue >= 0.5) return <StarHalfIcon key={i} />
                            else return <StarBorderIcon key={i} />
                        })
                    }
                </Grid >
            </div>
        </Grid >)
}

function Proficiency() {
    const classes = useStyles();
    const data = useSelector(state => state.proficiency.data);
    return (
        <Grid container className={classes.root} id="proficiency">
            <Typography variant="h2" className={classes.titleTexts}>
                Proficiency
            </Typography>
            <Grid container variant="row" spacing={3}>
                {
                    data.map((item, index) => {
                        return (
                            <ProficiencyCard
                                key={index}
                                item={item} />
                        )
                    })
                }
            </Grid>
        </ Grid >
    );
}

export default Proficiency;
