import { Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { useSelector } from "react-redux";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "85px 6vw 65px 6vw",
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px"
    },
    cardContainer: {
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "375px",
    },
    cardDescription: {
        padding: "15px",
        "& *": {
            textAlign: "center"
        }
    },
    cardImageContainer: {
        backgroundColor: "#e62958",
        width: "100%",
        height: "160px",
        position: "relative",
        marginBottom: "75px",
        borderTopLeftRadius: "22.5px",
        borderTopRightRadius: "22.5px",
        "& img":
        {
            width: "125px",
            height: "125px",
            position: "absolute",
            top: "100%",
            left: "50%",
            transform: "translate(-50%, -50%)",

        },
        "& h6": {
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -100%)",
            textAlign: "center",
            lineHeight: "35px",
            fontWeight: "500"
        }
    },
    cardDivider: {
        borderBottomWidth: "medium !important",
        maxWidth: "1000px"
    },

}));

function ExperiencesCard(props) {
    const classes = useStyles();
    const { imageSource, companyName, majorName, attendingDate } = props;
    return (
        <Grid item xs={4} >
            <div className={classes.cardContainer}>
                <div className={classes.cardImageContainer}>
                    <Typography variant="h6" >
                        {companyName}
                    </Typography>
                    <img src={imageSource} />
                </div>
                <div className={classes.cardDescription}>
                    <Typography variant="subtitle1" >
                        {majorName}
                    </Typography>
                    <Typography variant="subtitle1" >
                        {attendingDate}
                    </Typography>
                </div>
            </div>
        </Grid >)
}

function Experiences() {
    const classes = useStyles();
    const data = useSelector(state => state.experiences.data);
    return (
        <Grid container className={classes.root} id="experiences">
            <Typography variant="h2" className={classes.titleTexts}>
                Working experiences
            </Typography>
            <Grid container variant="row" spacing={3}>
                {
                    data.map((item, index) => {
                        return (
                            <ExperiencesCard
                                key={index}
                                imageSource={item.imageSource}
                                companyName={item.companyName}
                                majorName={item.majorName}
                                attendingDate={item.attendingDate} />
                        )
                    })
                }
            </Grid>
        </ Grid >
    );
}

export default Experiences;
