import { Button, Grid, Link, Typography, Toolbar, AppBar, Slide, useScrollTrigger, useMediaQuery } from "@mui/material";
import { makeStyles } from '@mui/styles';
import BadgeIcon from '@mui/icons-material/Badge';
import theme from "../theme";
import { useState } from "react";
import { useLayoutEffect } from "react";

const useStyles = makeStyles((_) => ({
    root: {
        height: "50px"
    },
    navigationContainer: {
        paddingRight: "25px ",
        "& *:hover": {
            cursor: "pointer",

        },
        "& *": {
            marginLeft: "35px !important"
        }
    },
    imageDisplayer: {
        width: "100%",
        height: "100%",
        top: 0,
        left: 0,
        position: "fixed",
        zIndex: "1",
        display: "flex",
        "& img": {
            width: "80vw",
            height: "80vh",
            zIndex: "2 !important",
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%,-50%)",
            objectFit: "contain"
        },
        "& div": {
            backgroundColor: theme.palette.primary.main,
            opacity: "0.8",
            position: "absolute",
            width: "100%",
            height: "100%",
            top: 0,
            left: 0,
            zIndex: "1 !important"
        }
    },
}));

function Header() {
    const classes = useStyles();
    const trigger = useScrollTrigger();
    const [isShowingImage, setIsShowingImage] = useState(false);
    const [shownImage, setShownImage] = useState("");
    useLayoutEffect(() => {
        console.log("a");
        document.querySelectorAll("img").forEach(element => {
            element.onclick = () => {
                setShownImage(element.src);
                setIsShowingImage(true);
            };
        });
    }, []);
    return (
        <Grid container direction={"column"} className={classes.root} >
            <Slide appear={false} direction="down" in={!trigger && !isShowingImage}>
                <AppBar position="fixed" color="secondary">
                    <Toolbar>
                        <Button
                            size="large"
                            edge="start"
                            color="inherit"
                            aria-label="menu"
                            sx={{ mr: 2 }}
                        >
                            <BadgeIcon fontSize="large" />
                        </Button>
                        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
                            Reza's Portfolio
                        </Typography>
                        <div className={classes.navigationContainer}>
                            <Link href="#about" underline="hover" color={"inherit"}>
                                About
                            </Link>
                            <Link href="#proficiency" underline="hover" color={"inherit"}>
                                Proficiency
                            </Link>
                            <Link href="#education" underline="hover" color={"inherit"}>
                                Education
                            </Link>
                            <Link href="#experiences" underline="hover" color={"inherit"}>
                                Experiences
                            </Link >
                            <Link href="#projects" underline="hover" color={"inherit"}>
                                Projects
                            </Link >
                            <Link href="#courses" underline="hover" color={"inherit"}>
                                Courses
                            </Link >
                            <Link href="#contact" underline="hover" color={"inherit"}>
                                Contact
                            </Link >
                        </div>
                    </Toolbar>
                </AppBar>
            </Slide>
            <Grid container className={classes.imageDisplayer} display={isShowingImage ? "block" : "none"} onClick={() => setIsShowingImage(false)}>
                <div />
                <img src={shownImage} />

            </Grid>
        </Grid >
    );
}

export default Header;
