import { Divider, Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { VerticalGap } from "../utils/gaps";
import { useSelector } from "react-redux";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "85px 6vw 65px 6vw",
    },
    titleTexts: {
        fontWeight: "bold !important",

    },
    subtitleTexts: {
    },
    cardDescription: {

    },
    cardImage: {
        width: "100px",
        height: "100px",
        marginRight: "25px"
    },
    cardDivider: {
        borderColor: "#62656c !important",
        borderBottomWidth: "medium !important",
        maxWidth: "1000px"
    },

}));

function EducationCard(props) {
    const classes = useStyles();
    const { imageSource, campusName, majorName, attendingDate } = props;
    const data = useSelector(state => state.education.data);
    return (
        <Grid item xs={12} >
            <Grid container  >
                <img className={classes.cardImage} src={imageSource} />
                <div className={classes.cardDescription}>
                    <Typography variant="h5" >
                        {campusName}
                    </Typography>
                    {/* <VerticalGap distance="5px" /> */}
                    <Typography variant="subtitle1" >
                        {majorName}
                    </Typography>
                    <Typography variant="subtitle1" >
                        {attendingDate}
                    </Typography>
                </div>
            </Grid>
            <VerticalGap distance={9} />
            <Divider className={classes.cardDivider} />
        </Grid >)
}

function Education() {
    const classes = useStyles();
    const data = useSelector(state => state.education.data);
    return (
        <Grid container rowSpacing={3} className={classes.root} id="education">
            <Typography variant="h2" className={classes.titleTexts}>
                Education
            </Typography>
            {
                data.map((item, index) => {
                    return (
                        <EducationCard
                            key={index}
                            imageSource={item.imageSource}
                            campusName={item.campusName}
                            majorName={item.majorName}
                            attendingDate={item.attendingDate} />
                    )
                })
            }
        </ Grid >
    );
}

export default Education;
